import React from 'react';
import {BrowserRouter} from "react-router-dom";
import ReactDOM from 'react-dom/client';
import App from './App';
import reportWebVitals from './reportWebVitals';
import {Provider} from 'react-redux';
import store from './store/store';
import {Helmet} from "react-helmet";
import fav from './img/notebook-dynamic-color.png';
import './index.css';


const root = ReactDOM.createRoot(document.getElementById('root'));
root.render (
  <BrowserRouter>
    <Provider store={store}>
      <App/>
      <Helmet>
        <meta charSet="utf-8"/>
        <title>Your Books</title>
        <meta name="description" content="Helmet application"/>

        <link rel="icon" type="image/png" href={fav} sizes="16x16"/> {/* <link rel="icon" href=". <link rel="icon" type="image/png" href="favicon.ico" sizes="16x16" /> <link rel="icon" type="image/png" href="favicon.ico" sizes="16x16" /> <link rel="icon" type="image/png" href="favicon.ico" sizes="16x16" />" /> */}
        {/* <link rel="canonical" href="http://mysite.com/example"/> */} </Helmet>
    </Provider>
  </BrowserRouter>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
